import * as React from 'react';
import { Container, Button, Grid, Typography, TextField } from '@material-ui/core';
import TranslateIcon from '@material-ui/icons/Translate';

interface WordProps {
    question: string;
    answer: string;
    precautions: string;
    notices: string;
    visibleWord: string;
    isInputWordCompleted: boolean;
    inputWordString:string;
    onClick: () => void;
    changeInputWordCompleted: (val:boolean) => void;
    changeInputWord: (val:string) => void;    
}

class Word extends React.Component<WordProps, {}>{
  render () {
    const style: React.CSSProperties = {
      display: 'inline-block',
      width: '100%',
      height: '20%',
    }
    
    return (
      <div className="word-wrap">
        <Container maxWidth="sm">
          <Grid container>
            <Grid item>
              <Typography variant='subtitle1'>
                {this.props.question}
              </Typography>
              {
                this.props.precautions
                &&
                <Typography variant='caption'>
                  ({this.props.precautions})
                </Typography>
              }
            </Grid>
          </Grid>
          <br/>
          <Grid container direction="row">
            <TextField
              error={!this.props.isInputWordCompleted}
              label= {this.props.isInputWordCompleted ? "OK" : "input answer"}
              fullWidth={true}
              value={this.props.inputWordString}
              variant="outlined"
              onChange={(o) => 
                {
                  this.props.changeInputWord(o.target.value.toLowerCase());
                  if (this.props.answer === o.target.value) {
                    this.props.changeInputWordCompleted(true);
                    this.props.onClick();
                  } else {
                    this.props.changeInputWordCompleted(false);
                  }
                }
              }
            />
          </Grid>
          <br />
          <Grid>
            <Button
              style={style}
              color="primary"
              variant="outlined"
              onClick={() => this.props.onClick()}
              >
                <TranslateIcon/>
            </Button>
          </Grid>
          <br />
          <Typography variant='h4' className={this.props.visibleWord}>
            {this.props.answer}
          </Typography>
          <hr />
          <Grid>
            <Typography variant='body1' align='left' className={this.props.visibleWord}>
              {this.props.notices}
            </Typography>
          </Grid>
          <br />
        </Container>
      </div>
    );
  }
}

export default Word;